@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(181, 215, 255);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  width: 100%;
}
.backsplash {
    position: absolute;
    width: 100vw;
    height: 100vh;
    z-index: -1;
}

.backsplash img {
    position: fixed;
    left: 0;
    bottom: 0;
    height: 70vh;
    opacity: 0.85;
    z-index: -1;
}

@media only screen and (max-width: 600px) {
    .backsplash {
        display: none;
    }
}
.about {
    font-family: 'Montserrat', sans-serif;
    position: absolute;
    top: 8%;
    right: 0;
    width: 95%;
    max-width: 700px;
    padding-right: 30px;
    padding-top: 60px;
    margin: 0 auto;
}

h2 {
    margin-left: 10px;
    font-weight: 300;
}

p {
    font-size: 19px;
    background-color: rgba(70, 80, 123, 0.733);
    border-radius: 10px;
    padding: 15px;
    color: white;
    font-weight: 300;
}

.backButton {
    position: absolute;
    top: 2%;
    left: 0;

    text-align: center;
    border: 1px solid rgb(70,80,123);
    padding: 10px 40px;
    text-decoration: none;
    color: rgb(70,80,123);
    font-weight: 400;
    transition: all 0.3s;
}

.backButton:hover {
    background-color: transparent;
    color: white;
    border: 1px solid rgb(70,80,123);
    background-color: rgb(70,80,123);
}


@media only screen and (max-width: 800px) {
    .about {
        position: inherit;
        padding-right: 0;
    }
    .backButton {
        position: absolute;
        top: 2%;
        left: 3%;
    }
}


@media only screen and (max-width: 1200px) {
    p {
        background-color: rgb(70, 80, 123);
    }
    h2 {
        background-color: rgb(255, 255, 255);
        padding: 7px;
        border-radius: 10px;
        font-weight: 300;
        margin-left: 0;
    }
}
.homepageMainContainer {
    position: absolute;
    top: 8%;
    right: 0;
    width: 85%;
    max-width: 700px;
    padding: 0 30px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 100;
}

h1 {
    font-weight: 200;
}

.introName {
    text-align: right;
    margin-bottom: 60px;
}

.introCareer {
    text-align: left;
    margin-bottom: 60px;
}

.introSkillsText {
    text-align: right;
    
}

.skillsContainer {
    text-align: right;
    margin-bottom: 60px;
}

.skillsContainer svg {
    width: 58px;
    margin-right: 15px;
}


.myProjectsInfoContainer {
    margin-bottom: 60px;
}

.myProjectsText {
    text-align: left;
}

.actionButton {
    text-align: center;
    border: 1px solid rgb(70,80,123);
    padding: 10px 40px;
    text-decoration: none;
    color: white;
    font-weight: 400;
    transition: all 0.3s;
    background-color: rgb(70,80,123);
}

.actionButton:hover {
    background-color: transparent;
    color: rgb(70,80,123);
    border: 1px solid rgb(70,80,123);
}


.aboutMeContainer {
    text-align: right;
}

.tooltipHome {
    color: white !important;
    font-weight: 500 !important;
}


@media only screen and (max-width: 700px) {
    .homepageMainContainer h1, .homepageMainContainer div {
        text-align: left;
    }
}

@media only screen and (max-width: 1000px) {
    .homepageMainContainer {
        background-color: rgba(255, 255, 255, 0.877);
        position: inherit;
        padding: 20px;
        margin: 0 auto;
        
    }
}


.footer {
    width: 100%;
    height: 30px;
    padding-bottom: 5px;
    position: fixed;
    bottom: 2%;
    right: 3%;

}

.footer img {
    height: 37px;
    float: right;
    margin: 0 7px;
    transition: all 0.25s;
    background-color: white;
    padding: 3px;
    border-radius: 50%;
}

.footer img:hover {
    transform: scale(1.25, 1.25);
}
.projects {
    width: 95%;
    max-width: 800px;
    position: absolute;
    right: 2%;
}

.projectsContainer {
    display: flex;
    margin-top: 85px;
    flex-wrap: wrap;
    align-self: flex-start;
}

.projectCard {
    max-width: 350px;
    margin: 10px;
}

.projectCard a {
    -webkit-text-decoration-color: white;
            text-decoration-color: white;
}

.projectCard img {
    width: 100%;
}

